import React from 'react';

const DeviceNotSupported = () => (
  <div className="errorMessage">
    <h1>Sorry,</h1>
    <h2>This site is mobile only.</h2>
    <p>Try again with an iOS or Android device.</p>
  </div>
);
export default DeviceNotSupported;



